<template>
  <div>

    <da-equal-panel left-width="160px" right-width="200px" height="auto">
      <da-panel padding="r" :no-head="true" class="text-center" slot="left">
        <div class="alarm ">
          <h3>告警数</h3>
          <P class="alarm-total">{{alarmCount}}</P>
        </div>
      </da-panel>
      <alarm-data slot="right" :dataLists="alarmLevelList" label="name" value-key="value" mode="vertical"></alarm-data>
      <div class="panel-flex-1 clearfix">
        <da-panel padding="lb" padding-content="lrtb" :no-head="true">
          <div class="inblock pad-r-10">
            <el-button class="link-a" type="text" @click="redirectHistory">历史告警查询</el-button>
            <el-button class="link-a" type="text" @click="redirectStatistics">告警统计和分析</el-button>
            <el-button class="link-a" type="text" @click="redirectConfigruction">关注告警配置</el-button>
          </div>
          <div class="inblock">
            <label>刷新倒计时：{{refreshTime}}秒</label>
            <label>刷新频率</label>
            <da-radio-select v-model="refreshRate" :maxNum="1" :option-lists="refreshRateList" label="name" value-key="id" @change="changeRefreshRate"></da-radio-select>
            <el-checkbox v-model=refreshChecked @change="changeCheck">{{dspRefresh}}</el-checkbox>
            <da-radio-select v-model="attentionAlarm" :maxNum="3" :option-lists="attentionAlarmList" label="name" value-key="id" @change="changeAttentionAlarm"></da-radio-select>
          </div>
        </da-panel>
        <da-panel padding="lb" :border="false" :no-head="true">
          <alarm-data :dataLists="exceptionCountList" width="20%" label="name" value-key="value" mode="horizontal"></alarm-data>
        </da-panel>
      </div>
    </da-equal-panel>

    <da-panel title="告警信息详细清单" width="100%" height="auto" :border="false" min-height="200px" padding="t" ref="panelAlarmDetail">
      <div slot="search">
        <label>告警级别：</label>
        <da-radio-select v-model="alarmLevel" :maxNum="1" :option-lists="alarmLevelNum" label="name" value-key="id" @change="changealArmLevel"></da-radio-select>
        <label>告警类型：</label>
        <da-radio-select v-model="alarmCategory" :maxNum="1" :option-lists="alarmCategoryList" label="name" value-key="id" @change="changeAlarmCategory"></da-radio-select>
        <span>说明：三类及四类告警无需确认</span>
      </div>
      <da-table class="analysis-table" :colums="alarmDetailcolumnData" :front-page="true" :table-list="alarmDetailtable">
        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="detail(scope.row)">详情</el-button>
          </template>
        </el-table-column>
      </da-table>
    </da-panel>

    <da-dialog title="告警详情" :visible.sync="dialogVisible" @close="closeDialog()">
      <el-form label-width="120px" :model="diaAlarmDetail" ref="diaAlarmDetail">
        <el-form-item label="告警类型" prop="alarmGroupName">
          <el-input auto-complete="off" v-model="diaAlarmDetail.alarmGroupName"></el-input>
        </el-form-item>
        <el-form-item label="告警详情" prop="alarmDetail">
          <el-input auto-complete="off" v-model="diaAlarmDetail.alarmDetail" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="所属部门" prop="orgId">
          <el-input auto-complete="off" v-model="diaAlarmDetail.orgId"></el-input>
        </el-form-item>
        <el-form-item label="计量名称" prop="meterName">
          <el-input auto-complete="off" v-model="diaAlarmDetail.meterName"></el-input>
        </el-form-item>
        <el-form-item label="数据项名称" prop="value">
          <el-input auto-complete="off" v-model="diaAlarmDetail.value"></el-input>
        </el-form-item>
        <el-form-item label="告警时间" prop="alarmTime">
          <el-date-picker v-model="diaAlarmDetail.alarmTime" type="date" format="yyyy-MM-dd"> </el-date-picker>
          <!-- <el-input auto-complete="off" v-model="diaAlarmDetail.alarmTime" format="YYYY-MM-DD"></el-input> -->
        </el-form-item>
        <!-- <el-form-item label="确认" prop="isConfirm">
                    <el-select v-model="diaAlarmDetail.isConfirm" filterable size="medium" placeholder="请选择">
                        <el-option v-for="(item, index) in confirList" :key="index" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item> -->
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog()">确认告警</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>

<script>
import alarmData from "../template/alarmData";
import { changeTree } from "../../../util/util";
export default {
  components: { alarmData },
  data() {
    return {
      allAlarmData: [], //所有告警详细数据
      alarmCount: 0, //告警数量
      alarmLevelList: [
        { value: 0, name: "一类告警", id: 0 },
        { value: 0, name: "二类告警", id: 1 },
        { value: 0, name: "三类告警", id: 2 },
        { value: 0, name: "四类告警", id: 3 }
      ], //告警登记列表
      refreshTime: 1, //刷新时间
      refreshRate: "", //刷新频率
      refreshRateList: [
        { id: 10, name: "10秒" },
        { id: 30, name: "30秒" },
        { id: 60, name: "60秒" }
      ], //刷新的时间组
      refreshChecked: false, //是否定时刷新
      dspRefresh: "关",
      exceptionCountList: [], //告警种类列表
      attentionAlarm: "", //我关注的告警
      selectAlarmCode: "", //选择的关注的告警
      selectAttentionAlarm: "", //选择关注的告警
      attentionAlarmList: [
        { id: 0, name: "所有的告警" },
        { id: 1, name: "我关注的告警" }
      ], //我关注的告警列表
      alarmLevel: "", //告警级别
      selectAlarmLevel: null, //选择告警的级别
      alarmLevelNum: [
        { name: "全部", id: 0 },
        { name: "一类", id: 1 },
        { name: "二类", id: 2 },
        { name: "三类", id: 3 },
        { name: "四类", id: 4 }
      ], //告警级别列表
      alarmCategory: "", //告警种类
      selectAlarmCategory: "", //选择告警种类
      alarmCategoryList: [], //告警类型列表
      alarmDetailcolumnData: [
        {
          label: "告警类型",
          prop: "alarmGroupName"
        },
        {
          label: "告警详情",
          prop: "alarmDetail",
          subStr: true,
          popover: true
        },
        {
          label: "所属部门",
          prop: "orgId",
          displace: {}
        },
        {
          label: "计量点名称",
          prop: "meterName"
        },
        {
          label: "数据项名称",
          prop: "value"
        },
        {
          label: "告警时间",
          prop: "alarmTime",
          format: "YYYY-MM-DD"
        }
        // {
        //   label: "操作",
        //   prop: "isConfirm",
        //   displace: { 0: "未确认", 1: "确认" }
        // }
      ],
      alarmDetailtable: [],
      dialogVisible: false,
      diaAlarmDetail: {
        alarmGroupName: "",
        alarmDetail: "",
        orgId: "",
        meterName: "",
        value: "",
        alarmTime: "",
        isConfirm: ""
      }
    };
  },
  methods: {
    //初始化数据
    inintData() {
      this.getAttentionAlarm();
      this.getDepartmentList();
    },
    //加载页面
    loadData() {
      this.getAlarmLevelCount();
      this.getAlarmDetailtable();
      this.getExceptionCountList();
    },
    //获得告警种类类型数据
    getExceptionCountList() {
      this.$ajax
        .get("rest/analysisData/alarmGroup", {
          params: {
            title: "查询告警种类" //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            this.$each(res.data.records, item => {
              data.push({
                name: item.name,
                idValue: item.id,
                value: 0
              });
            });
            this.exceptionCountList = data;
            data = [{ name: "全部", idValue: "", value: 0 }].concat(data);
            this.$each(data, (item, i) => {
              this.alarmCategoryList.push({
                name: item.name,
                idValue: item.idValue,
                id: i
              });
            });
            this.selectAlarmCategory = this.alarmCategoryList[0].idValue;
            this.getAlarmCategoryCount();
          }
        });
    },
    //获得我关注的告警类型
    getAttentionAlarm() {
      this.$ajax
        .get("rest/analysisData/alarmItem", {
          params: {
            title: "关注的告警", //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
            userIds: null, //用户ID，为空表示不作为查询条件，多个用逗号隔开，如"1,2,..."
            alarmGroups: null, //告警分组ID，为空表示不作为查询条件，多个用逗号隔开，如"1,2,..."
            receiveTypes: 1, //用户接收告警类型，1:弹窗；2：邮件；3：短信；4：微信，为空时表示不作为查询条件
            isAlarmGroup: null, //是否返回告警分组
            isReceiveType: true //是否返回用户接收告警类型
          }
        })
        .then(res => {
          if (res.data.status) {
            for (let i = 0; i < res.data.records.length; i++) {
              if (i < res.data.records.length - 1) {
                this.selectAlarmCode += res.data.records[i].id + ",";
              } else {
                this.selectAlarmCode += res.data.records[i].id;
              }
            }
          }
        });
    },

    //跳转到历史告警
    redirectHistory() {
      this.$router.push({ path: "/alarmHistorySearch" });
    },
    //跳转到告警统计分析
    redirectStatistics() {
      this.$router.push({ path: "/alarmStatisticsAnalysis" });
    },
    redirectConfigruction() {
      console.log(22);
      this.$router.push({ path: "/alarmConfiguration" });
    },
    //钩选刷新标签
    changeRefreshRate() {
      this.refreshTime = parseInt(this.refreshRate);
    },
    changeCheck() {
      if (this.refreshChecked) {
        this.dspRefresh = "开";
      } else {
        this.dspRefresh = "关";
      }
    },
    //一二类告警数据统计和 总告警统计*
    getAlarmLevelCount() {
      let startTime = new Date(2000, 0, 1);
      startTime = this.$moment(startTime, "YYYY-MM-DD");
      let endTime = this.$moment(new Date(), "YYYY-MM-DD");
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警级别统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: false, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: startTime, // String,开始时间
            endTime: endTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "010000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = res.data.records;
            if (data !== null) {
              //统计告警总数
              this.alarmCount = 0;
              for (let i = 0; i < data.length; i++) {
                this.alarmCount += data[i].alarmNum;
              }
              for (let i = 0; i < data.length; i++) {
                for (let j = 0; j < this.alarmLevelList.length; j++) {
                  if (data[i].level == this.alarmLevelList[j].id + 1) {
                    this.alarmLevelList[j].value = data[i].alarmNum;
                  }
                }
              }
            }
          }
        });
    },

    //各类告警类型数据统计*
    getAlarmCategoryCount() {
      let startTime = new Date(2000, 0, 1);
      startTime = this.$moment(startTime, "YYYY-MM-DD");
      let endTime = this.$moment(new Date(), "YYYY-MM-DD");
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警类型统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: this.selectAlarmCategory, // String,告警分组ID,告警类型
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: false, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: startTime, // String,开始时间
            endTime: endTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "001000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = res.data.records;
            if (data !== null) {
              for (let i = 0; i < this.exceptionCountList.length; i++) {
                for (let j = 0; j < data.length; j++) {
                  if (
                    this.exceptionCountList[i].name == data[j].alarmGroupName
                  ) {
                    this.exceptionCountList[i].value = data[j].alarmNum;
                  }
                }
              }
            }
          }
        });
    },

    //点击我关注的告警
    changeAttentionAlarm() {
      if (this.attentionAlarm == 0) {
        this.selectAttentionAlarm = "";
      } else {
        this.selectAttentionAlarm = this.selectAlarmCode;
      }
      this.loadData();
    },
    //切换告警级别
    changealArmLevel() {
      switch (this.alarmLevel) {
        case 0:
          this.selectAlarmLevel = "";
          break;
        case 1:
          this.selectAlarmLevel = "1";
          break;
        case 2:
          this.selectAlarmLevel = "2";
          break;
        case 3:
          this.selectAlarmLevel = "3";
          break;
        case 4:
          this.selectAlarmLevel = "4";
          break;
      }
      this.getAlarmDetailtable();
    },
    //切换告警类型
    changeAlarmCategory() {
      for (let i = 0; i < this.alarmCategoryList.length; i++) {
        if (this.alarmCategory == this.alarmCategoryList[i].id) {
          this.selectAlarmCategory = this.alarmCategoryList[i].idValue;
          break;
        }
      }
      this.getAlarmDetailtable();
    },
    //获得部门详情列表
    getDepartmentList() {
      this.$ajax
        .get("rest/analysisData/depart", {
          params: {
            title: "查询部门列表", //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
            orgTypes: null, //部门类型，多个用逗号隔开，可为空，为空不作为查询条件
            parentIds: null //上级部门ID，多个用逗号隔开，可为空，为空不作为查询条件
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            this.$each(res.data.records, item => {
              data.push({
                name: item.name,
                orgId: item.id
              });
            });
            let obj = {};
            data.forEach(item => {
              obj[item.orgId] = item.name;
            });
            this.alarmDetailcolumnData[2].displace = obj;
          }
        });
    },

    //告警信息详细清单*
    getAlarmDetailtable() {
      let startTime = new Date(2000, 0, 1);
      startTime = this.$moment(startTime, "YYYY-MM-DD");
      let endTime = this.$moment(new Date(), "YYYY-MM-DD");
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警详细信息", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: this.selectAlarmLevel, // String,告警级别
            alarmGroups: this.selectAlarmCategory, // String,告警分组ID,告警类型
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: false, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: startTime, // String,开始时间
            endTime: endTime, // String,结束时间
            isCount: false, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "101001100" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            this.alarmDetailtable = res.data.records;
          }
        });
    },

    detail(row) {
      this.dialogVisible = true;
      this.diaAlarmDetail = JSON.parse(JSON.stringify(row));
    },
    closeDialog() {
      this.$nextTick(function() {
        this.diaAlarmDetail = {
          alarmGroupName: "",
          alarmDetail: "",
          orgId: "",
          meterName: "",
          value: "",
          alarmTime: "",
          isConfirm: ""
        };
      });
    },
    saveDialog() {
      //修改查询
      let alarm = {
        id: "" // 告警数据表
        // entId: "", // 企业ID
        // alarmTime: "", // 告警时间
        // isConfirm: "", // 是否确认，0否，1是
        // confirmor: 1 ,// 确认人，关联用户表ID
        // meterItemId: "", // 数据项ID,meter_item
        // alarmItemId: "", // 告警项ID，alarm_item
        // alarmDetail: "", // 告警详情
        // countDay: "", // 同类告警数日总量
        // alarmEndtime: "", // 告警消除时间
        // remark: "" // 备注信息
      };
      alarm.id = this.diaAlarmDetail.id;
      this.$ajax.post("rest/alarm/confirm", alarm).then(res => {
        if (res.data.status) {
          this.$message.success(res.data.answerMsg);
          this.dialogVisible = false;
        } else {
          this.$message.error(res.data.answerMsg);
        }
        this.loadData();
      });
    }
  },
  mounted() {
    this.inintData();
    this.loadData();
    //定时刷新
    setInterval(() => {
      if (this.refreshTime > 0) {
        this.refreshTime--;
      } else {
        this.refreshTime = parseInt(this.refreshRate);
        if (this.refreshChecked == true) {
          this.loadData();
        }
      }
    }, 1000);
  }
};
</script>
<style>
.alarm {
  padding: 40px 0;
  line-height: 43px;
  font-size: 25px;
}
.alarm-total {
  color: red;
  font-weight: bold;
}
</style>


