<template>
  <div :class="{'ad-wrap-vertical':mode==='vertical','ad-wrap-horizontal clearfix':mode!=='vertical','ad-border':'border'}" :style="mode==='vertical'?'width:'+width:''">
    <div class="inblock" v-for="(item, index) in dataLists" :key="index" :style="mode==='vertical'?'':'width:'+width">
      <div class="ad-item">
        <span class="ad-item-label" :style="mode==='vertical'?'':'background-color:'+color[index%color.length]">{{item[label]}}</span>
        <span class="ad-item-value" :style="mode==='vertical'?'background-color:'+color[index%color.length]:''">{{item[valueKey]}}</span>
      </div>
    </div>
  </div>
</template>
   
<script>
export default {
  props: {
    dataLists: {
      type: Array
    },
    mode: {
      type: String, // horizontal / vertical
      default: "vertical"
    },
    label: {
      type: String,
      default: "name"
    },
    valueKey: {
      type: String,
      default: "id"
    },
    width: {
      type: String,
      default: "200px"
    },
    border: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      color: [
        "#0C7E0E",
        "#426896",
        "#AF399C",
        "#DC4D5B",
        "#4617B4",
        "#3B65BF",
        "#177D86",
        "#2D004E",
        "#00D8CC",
        "#004060",
        "#4E0038",
        "#33CC00",
        "#E36B19"
      ]
    };
  }
};
</script>

<style scoped>
/*horizontal*/
.ad-wrap-horizontal.ad-border .ad-item {
  border: 1px solid #a8c8f9;
}
.ad-wrap-horizontal {
  margin: -5px;
}
.ad-wrap-horizontal .inblock {
  padding: 5px;
}
.ad-wrap-horizontal .ad-item {
  float: left;
  width: 100%;
}
.ad-wrap-horizontal .ad-item-label,
.ad-wrap-horizontal .ad-item-value {
  font-size: 16px;
  padding: 8px 0;
  min-width: 120px;
  color: #fff;
  display: block;
  text-align: center;
}
.ad-wrap-horizontal .ad-item-value {
  color: #444;
}
/*vertical*/
.ad-wrap-vertical .inblock {
  display: block;
}
.ad-wrap-vertical.ad-border {
  border-right: 1px solid #78adfe;
  border-bottom: 1px solid #78adfe;
}
.ad-wrap-vertical.ad-border .ad-item {
  border-left: 1px solid #78adfe;
  border-top: 1px solid #78adfe;
}
.ad-wrap-vertical .ad-item {
  display: flex;
  padding: 8px 5px;
}
.ad-wrap-vertical .ad-item-label {
  width: 50%;
  padding: 3px 0;
  text-align: center;
}
.ad-wrap-vertical .ad-item-value {
  flex: 1;
  padding: 3px 10px;
  width: 50%;
  color: #fff;
  text-align: center;
}
</style>



